const siteTitle = "Evan Greenberg"
const siteDescription = "Personal Website of Evan Greenberg"

const HomeIndex = ({ siteTitle, siteDescription, data }) => (
    <Layout>
        <Helmet>
                <title>{siteTitle}</title>
                <meta name="description" content={siteDescription} />
        </Helmet>

        <div id="main">
            <section id="one">
                <header className="major">
                    <h2>Welcome!</h2>
                </header>
                <p
                    dangerouslySetInnerHTML={{ __html: data.Bio.html }}
                >
                </p>
                <ul className="actions">
                    <li><a href="cv" className="button">Full CV</a></li>
                </ul>
            </section>
        </div>

    </Layout>
)

export default HomeIndex

export const query = graphql`
  query {
     Bio: markdownRemark(frontmatter: { title: { eq: "bio" } }) {
        html
		frontmatter {
			title 
			date
		}
	},
    BighornProject: markdownRemark(frontmatter: { title: { eq: "River Mobility and Climate Change in Ancient Rivers"} }) {
        html
    }
  }
`
